import axios from "axios";

// Determine the base API URL depending on environment
const apiUrl = process.env.NODE_ENV === "development"
    ? "http://localhost:8000" // Local development server
    : "https://gamebits-579c6fd85599.herokuapp.com"; // Production server (heroku)

// Set the base URL for all axios requests to the API endpoint
axios.defaults.baseURL = `${apiUrl}/api/`;
// Default Content-Type for POST requests is multipart/form-data
axios.defaults.headers.post["Content-Type"] = "multipart/form-data";
// Allow axios to send cookies with requests (needed for some auth setups)
axios.defaults.withCredentials = true;

//Add a request interceptor to inject JWT token into Authorization header if available
axios.interceptors.request.use((config) => {
    const token = localStorage.getItem("access_token");
    if (token) {
        config.headers["Authorization"] = `Bearer ${token}`;
    }
    return config;
}, (error) => {
    return Promise.reject(error);
});

export default axios;